.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    justify-items: right;
    align-items: center;

    padding: 1rem 2rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: right;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: right;
    /* margin-right: 1rem; */
}
.nav{
    margin-right: 2rem !important;
}
.button {
    background-color: transparent;
    color: rgb(100, 255, 218);
    border: 1px solid rgb(100, 255, 218); /* Green */
    font-size: 15px !important;
}

.button:hover {
background-color: rgb(100, 255, 218);
color: white;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 1000;
    font-size: 14px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 0rem;
    cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    padding: 0.5rem 1rem;
    /* color: #fff; */
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 15px;
    line-height: 25px;
    /* border: none; */
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.gpt3__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-menu {
        display: none;
    }

    /* .gpt3__navbar-menu {
        display: flex;
    } */
}

/* @media screen and (max-width: 700px) {
    .gpt3__navbar-menu {
        display: flex;
    }
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
} */

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem;
    }
    .gpt3__navbar-menu {
        display: flex;
    }
    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}